// Load in our dependencies
@use '_floorplan.scss';
@use '../../node_modules/react-resizable/css/styles.css';
@import '_location-colors.scss';

// Custom Bootstrap extensions
// Override normal container behavior, focused on desktop only app
.container {
  width: 1140px;
  max-width: none;
}

.progressbar-muted {
  background-color: transparent;
  color: black;
  opacity: 0.4;
}

.row-cols-8 > * {
    -ms-flex: 0 0 12.5%;
    flex: 0 0 12.5%;
    max-width: 12.5%;
}

// One-off style fixes to allow input group to look akin to a button grop with different layout
.input-group-as-btn-group {
  .input-group-text {
    border: 0;
  }
  .form-control-plaintext {
    border-radius: 0.25rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;

    text-align: left;
    padding: .375rem .75rem;
    overflow: hidden;
    white-space: nowrap;
  }
}

// Allow button to have no default styles are all (useful for a11y)
// https://github.com/tailwindlabs/tailwindcss/blob/v1.9.6/src/plugins/css/preflight.css#L27-L40
// https://github.com/tailwindlabs/tailwindcss/blob/v1.9.6/src/plugins/css/preflight.css#L165-L181
.btn-unstyled {
  background-color: transparent;
  background-image: none;
  // Fix for Firefox/IE, see source for reference
  &:focus {
    outline: 1px dotted;
    outline: 5px auto -webkit-focus-ring-color;
  }
  padding: 0;
  line-height: inherit;
  color: inherit;
}

// Custom CSS styles
.location-1-bg { background-color: $location-1-bg; color: $location-1-fg; }
.location-2-bg { background-color: $location-2-bg; color: $location-2-fg; }
.location-3-bg { background-color: $location-3-bg; color: $location-3-fg; }
.location-4-bg { background-color: $location-4-bg; color: $location-4-fg; }
.location-5-bg { background-color: $location-5-bg; color: $location-5-fg; }
.location-6-bg { background-color: $location-6-bg; color: $location-6-fg; }
.location-7-bg { background-color: $location-7-bg; color: $location-7-fg; }
.location-8-bg { background-color: $location-8-bg; color: $location-8-fg; }
.location-9-bg { background-color: $location-9-bg; color: $location-9-fg; }
.location-0-bg { background-color: $location-0-bg; color: $location-0-fg; }

.location-img::before { display: block; position: absolute; text-align: center; font-size: 12px; padding: 0 4px; }
.location-1-img { border: 2px solid $location-1-bg; }
.location-1-img::before { content: '1'; background-color: $location-1-bg; color: $location-1-fg; }
.location-2-img { border: 2px solid $location-2-bg; }
.location-2-img::before { content: '2'; background-color: $location-2-bg; color: $location-2-fg; }
.location-3-img { border: 2px solid $location-3-bg; }
.location-3-img::before { content: '3'; background-color: $location-3-bg; color: $location-3-fg; }
.location-4-img { border: 2px solid $location-4-bg; }
.location-4-img::before { content: '4'; background-color: $location-4-bg; color: $location-4-fg; }
.location-5-img { border: 2px solid $location-5-bg; }
.location-5-img::before { content: '5'; background-color: $location-5-bg; color: $location-5-fg; }
.location-6-img { border: 2px solid $location-6-bg; }
.location-6-img::before { content: '6'; background-color: $location-6-bg; color: $location-6-fg; }
.location-7-img { border: 2px solid $location-7-bg; }
.location-7-img::before { content: '7'; background-color: $location-7-bg; color: $location-7-fg; }
.location-8-img { border: 2px solid $location-8-bg; }
.location-8-img::before { content: '8'; background-color: $location-8-bg; color: $location-8-fg; }
.location-9-img { border: 2px solid $location-9-bg; }
.location-9-img::before { content: '9'; background-color: $location-9-bg; color: $location-9-fg; }
.location-0-img { border: 2px solid $location-0-bg; }
.location-0-img::before { content: '0'; background-color: $location-0-bg; color: $location-0-fg; }
.selected-image {
  &,
  &:focus {
    outline: 2px solid black;
  }
}
